import { Component, Inject, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import {
  CourseData,
  IAddCourseRequest,
} from "app/admin/models/admin/course-request";
import { LanguageService } from "app/shared/services/language.service";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";

@Component({
  selector: "app-add-course-request-details",
  templateUrl: "./add-course-request-details.component.html",
  styleUrls: ["./add-course-request-details.component.scss"],
})
export class AddCourseRequestDetailsComponent implements OnInit {
  myForm: FormGroup;
  submitted: boolean = false;
  course: CourseData;

  constructor(
    public languageService: LanguageService,
    private fb: FormBuilder,
    public dialogRef: MatDialogRef<AddCourseRequestDetailsComponent>,
    @Inject(MAT_DIALOG_DATA) public data: { course: CourseData }
  ) {
    this.course = data.course;
  }

  ngOnInit(): void {
    this.myForm = this.fb.group({
      reason: [null, [Validators.required, CustomeValidator.whiteSpace]],
      durationExpected: [null, [Validators.required, CustomeValidator.bigZero]],
    });
  }

  get formCtrl() {
    return this.myForm.controls;
  }

  addRequestDetails() {
    this.submitted = true;
    // console.log(this.myForm.value);
    if (this.myForm.valid) {
      let addRequestDetails: IAddCourseRequest = {
        courseId: this.course.id,
        reasonOfRequest: this.formCtrl?.reason.value,
        numberOfDays: this.formCtrl?.durationExpected?.value,
      };

      this.dialogRef.close(addRequestDetails);
    }
  }
}
